import React from 'react';
import s from './Post.module.css';

type PostPropsType={
   id?: string
    message: string
    likesCount: number
}

const Post = (props: PostPropsType) => {
    return <div className={s.item}>
    <img src='data:image/jpeg;base64,'/>
        {props.message}
        <div>
        <span>Likes {props.likesCount}</span>
    </div>
    </div>
}
export default Post;